import uuid from "react-uuid"

export const addtodo=(data)=>{
    return {
        type:"ADD_TODO",
        payload:{
            id:uuid(),
            data:data
        }
    }
}

export const deleteTodo=(id)=>{
    //console.log(id);
    return {
        type:"DELETE_TODO",
       id
    }
}

export const removeTodo=()=>{
    return {
        type:"REMOVE_TODO"
    }
}