import * as React from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import styles from "./Todolist.css"
import { border, flexbox, padding } from '@mui/system';
import Button from '@mui/material/Button';
import { Typography } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import { useState } from 'react';
import {addtodo,deleteTodo,removeTodo} from "../Actions/index"
import { useDispatch,useSelector } from 'react-redux';
import Mylist from  "./Mylist.css";
import DeleteIcon from '@mui/icons-material/Delete';

export default function FullWidthTextField(props) {
    const[InputData,setInputData]=useState();

    const dispatch=useDispatch(); 
    const list=useSelector((state)=>{return state.todoReducers.list})
    //  console.log("i got data",list)
    const inputHandler=(e)=>{
setInputData(e.target.value);
//console.log(InputData);
    }
 
  return (
      <div className='container'>
     
    <Box
      sx={{
          display:flexbox, 
        width: 500,
        maxWidth: '100%',
        
      }}
    >
        <Typography variant="h5" sx={{marginBottom:3,textAlign:'center',}}> MY ToDo List </Typography>

      <TextField fullWidth label="Work_to be done" id="User_Name" name="User_Name" sx={{marginBottom:2}}  value={InputData||""} onChange={inputHandler}/> 
      <Button variant="outlined" onClick={()=>{dispatch(addtodo(InputData),setInputData(""))}} >ADD</Button>

      {list.map((elem)=>{
          return(
              <div className='mapList' key={elem.id}>
                  <h3>{elem.data}</h3>
                 <button onClick={()=>{dispatch(deleteTodo(elem.id))}}><DeleteIcon/></button>
              </div>
          )
      })}
     
    </Box>

    </div>
  );
}